<template>
<!--  -->
<div class="user-table">
<el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
</el-breadcrumb>
  <!--  -->
  <el-card>
  <!--  -->
    <el-row :gutter="20">
    <el-col :span='8'>
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
    </el-col>
    <el-col :span='4'>
        <el-button type="primary" @click="addDialogVisible=true" ></el-button>
    </el-col>
  </el-row>
  <!--  -->
<el-table :data="userlist" border stripe  >
    <el-table-column type="index" label="#"></el-table-column>
    <el-table-column label="" prop="username"></el-table-column>
    <el-table-column label="" prop="email"></el-table-column>
    <el-table-column label="" prop="mobile"></el-table-column>
    <el-table-column label="" prop="role_name"></el-table-column>
    <el-table-column label="" >
       <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
           </el-switch>                          
       </template>
    </el-table-column>
    <el-table-column label="" >
      <template slot-scope="scope">
                <!--  -->   
            <el-tooltip  effect="light" content="" placement="top" :enterable='false' >
                <el-button type="primary" icon="el-icon-edit" circle size="mini" @click="showEditDialog(scope.row.id)"></el-button>
            </el-tooltip>
                <!--  -->
            <el-tooltip  effect="dark" content="" placement="top" :enterable='false'>
              <el-button type="danger" icon="el-icon-delete" circle size="mini" @click="removeUserById(scope.row.id)"></el-button>
            </el-tooltip>
                <!--  -->
            <el-tooltip  effect="dark" content="" placement="top" :enterable='false'>
              <el-button type="warning" icon="el-icon-setting" circle size="mini" @click="setRole(scope.row)"></el-button>
            </el-tooltip>
      </template>
    </el-table-column>

</el-table>
<!--  -->
    <el-pagination  @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
<!--  -->
<el-dialog
  title=""
  :visible.sync="addDialogVisible"
  width="50%" @close="adddialogClose">
        <el-form ref="addFormRefs" :model="addForm" label-width="70px" :rules="addFormRules" >
         <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
        </el-form>
 
  <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addUser"> </el-button>
  </span>
</el-dialog>
<!--  -->
<el-dialog
  title=""
  :visible.sync="editDialogVisible"
  width="35%"
  @close="editDialogClosed">
<el-form ref="editFormRef" :model="editForm" label-width="70px" :rules="editFormRules">
  <el-form-item label="">
    <el-input v-model="editForm.username" disabled></el-input>
  </el-form-item>
    <el-form-item label="" prop="email">
    <el-input v-model="editForm.email" ></el-input>
  </el-form-item>
    <el-form-item label="" prop="mobile">
    <el-input v-model="editForm.mobile" ></el-input>
  </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editUserInfo"> </el-button>
  </span>
</el-dialog>
<!--  -->
        <el-dialog
          title=""
          :visible.sync="setRoleDialogVisible"
          width="40%"
          @close="setRoleDialogClose"
          >
                <div>
                  <p>:{{userInfo.username}}</p>
                  <p>:{{userInfo.role_name}}</p>
                  <p>:
                        <el-select v-model="selecteRoleId" placeholder="">
                           <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id"></el-option>
                        </el-select>
                  </p>

                </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="setRoleDialogVisible = false"> </el-button>
            <el-button type="primary" @click="savaRoleInfo"> </el-button>
          </span>
        </el-dialog>
</div>

</template>

<script>
export default {
data() {
      // 
    var checkEmail = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/

      if (regEmail.test(value)) {
        // 
        return cb()
      }

      cb(new Error(''))
    }

    // 
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/

      if (regMobile.test(value)) {
        return cb()
      }

      cb(new Error(''))
    }
  
  return {
    //
    addForm:{
      username:'',
      password:'',
      email:'',
      mobile:'',
    },
    //
    queryInfo:{
      query:'',
      pagenum:1,
      //
      pagesize:2,
  
    },
    editDialogVisible:false,
    userlist:[],
    total:0,
  addDialogVisible:false,
  // 
  setRoleDialogVisible:false,
  userInfo:{},
  rolesList:[],
  //id
  selecteRoleId:'',
	//
	editForm:{},
    addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
	  },
	  //
	   editFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
  }
},
created() {
  this.getUserList()
},
methods: {
  async getUserList(){
    const {data:res} = await this.$http.get('users',{params:this.queryInfo})
    // console.log(res);
    if(res.meta.status!==200) return this.$message.error('')
    this.userlist =res.data.users
    this.total=res.data.total
  },
    //pagesize 
     handleSizeChange(newSize){
          this.queryInfo.pagesize=newSize
          this.getUserList()
          },
          // 
     handleCurrentChange(newPage){
          this.queryInfo.pagenum=newPage
          this.getUserList()
          },
          async userStateChanged(val){
           const {data:res} =await this.$http.put(`users/${val.id}/state/${val.mg_state}`)
           if(res.meta.status!==200) {
             val.mg_state=!val.mg_state
             return this.$message.error('')
           }
           this.$message.success('')
          },

          adddialogClose(){
            this.$refs.addFormRefs.resetFields()
            // this.getUserList()
          }
          ,
     addUser() {
        this.$refs.addFormRefs.validate(async valid => {
          if (!valid) return
          // 
          const { data: res } = await this.$http.post('users', this.addForm)

          if (res.meta.status !== 201) {
            this.$message.error('')
          }
          this.$message.success('')
          // 
          this.addDialogVisible = false
          // 
          this.getUserList()
        })
    },
    // 
  async  showEditDialog(id){
		const {data:res} =await this.$http.get('users/'+id)
		if(res.meta.status!==200) return this.$message.error('')
		 this.editForm=res.data
		
		this.editDialogVisible=true
        
    },
//
editDialogClosed(){
	this.$refs.editFormRef.resetFields()
},
//
editUserInfo(){
	//
	this.$refs.editFormRef.validate(async valid=>{
		if(!valid) return 
	const {data:res} = await this.$http.put('users/'+this.editForm.id,{email:this.editForm.email,mobile:this.editForm.mobile})
	if(res.meta.status!==200) return this.$message.error('')
	//
	this.editDialogVisible=false
	//
	this.getUserList()
	// 
	this.$message.success('')
	})
},
//id
		async removeUserById(id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)

      //  confirm
      //  cancel
      // console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete('users/' + id)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getUserList()
    },
    // 
 async setRole(userInfo){
       this.userInfo=userInfo
   const {data:res} = await this.$http.get('roles')
   if(res.meta.status!==200){
     return this.$message.error('!')
   }
    this.rolesList=res.data
        this.setRoleDialogVisible=true
    },
    // 
   async savaRoleInfo(){
      if(!this.selecteRoleId){
        return this.$message.error('  ')
      }
   const {data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{rid:this.selecteRoleId })
   if(res.meta.status!==200) return this.$message.error('')
   this.$message.success('')
   this.getUserList()
   this.setRoleDialogVisible =false
    },
    // 
          setRoleDialogClose(){
            this.selecteRoleId=''
            this.userInfo=''
          }
   }
}
</script>

<style lang="less" scoped>
   .el-table {
            /deep/ th{
              text-align: center;
            }
            }
            .el-table{
              /deep/ td{
                text-align: center;
              }
            }

     
</style>