<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <el-card>
      <el-alert
        title=""
        type="warning"
        show-icon
        :closable="false"
      >
      </el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span></span>
          <!--  -->
          <el-cascader
            expand-trigger="hover"
            :options="catelist"
            :props="cateProps"
            v-model="selectedCateKeys"
            @change="handleChange"
          >
          </el-cascader>
        </el-col>
      </el-row>
      <!-- tabs -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="" name="many">
          <el-button
            type="primary"
            @click="addDialogVisible = true"
            :disabled="isBtnDisabled"
            ></el-button
          >
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- Tag -->
                <el-tag
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close="handleClose(i, scope.row)"
                  >{{ item }}</el-tag
                >
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"> </el-table-column>
            <el-table-column label="" prop="attr_name">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeDialog(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="only">
          <el-button
            type="primary"
            :disabled="isBtnDisabled"
            @click="addDialogVisible = true"
              @close="handleClose(i, scope.row)" 
            ></el-button
          >
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- Tag -->
                <el-tag
                  v-for="(item, i) in scope.row.attr_vals"
                  :key="i"
                  closable
                  @close="handleClose(i,scope.row)"
                  >{{ item }}</el-tag
                >
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <!--  -->
                <el-button
                  v-else
                  class="button-new-tag"
                  size="small"
                  @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <el-table-column type="index"> </el-table-column>
            <el-table-column label="" prop="attr_name">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  @click="showEditDialog(scope.row.attr_id)"
                  ></el-button
                >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  @click="removeDialog(scope.row.attr_id)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="40%"
      @close="addDialogClose"
    >
      <el-form
        :model="CateForm"
        :rules="addFormRules"
        ref="cateFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="CateForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addPramas"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="40%"
      @close="editDialogClose"
    >
      <el-form
        :model="editCateForm"
        :rules="editFormRules"
        ref="editcateFormRef"
        label-width="100px"
      >
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editCateForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editPramas()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import LoginVue from "../Login.vue";
export default {
  data() {
    return {
      catelist: [],
      cateProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      selectedCateKeys: [],
      activeName: "many",
      //
      manyTableData: [],
      // 
      onlyTableData: [],
      addDialogVisible: false,
      editDialogVisible: false,
      CateForm: {
        attr_name: "",
      },
      editCateForm: {
        attr_name: "",
      },
      addFormRules: {
        attr_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      editFormRules: {
        attr_name: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  created() {
    this.getCatelist();
  },
  methods: {
    // 
    async getCatelist() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200)
        return this.$message.error("");

      this.catelist = res.data;
    },
    // ,
    async getParmasList() {
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = [];
        this.manyTableData = [];
        this.onlyTableData = [];
        return;
      }
      //   
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName },
        }
      );

      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(",") : [];
        item.inputVisible = false;
        item.inputValue = "";
      });
      if (res.meta.status !== 200) return this.$message.error("");

      if (this.activeName === "many") {
        this.manyTableData = res.data;
      } else {
        this.onlyTableData = res.data;
      }
    },
    //
    handleChange() {
      this.getParmasList();
    },
    //tab

    handleClick() {
      this.getParmasList();
    },
    //
    addDialogClose() {
      this.$refs.cateFormRef.resetFields();
    },
    // 
    editDialogClose() {
      this.$refs.editcateFormRef.resetFields();
    },
    //
    addPramas() {
      // 
      this.$refs.cateFormRef.validate(async (valid) => {
        if (!valid) return;
        // 
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.CateForm.attr_name,
            attr_sel: this.activeName,
          }
        );
        if (res.meta.status !== 201)
          return this.$message.error("");
        this.$message.success("");
        this.addDialogVisible = false;
        this.getParmasList();
      });
    },
    // 
    async showEditDialog(attr_id) {
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${attr_id}`,
        {
          params: { attr_sel: this.activeName },
        }
      );

      if (res.meta.status !== 200) {
        return this.$message.error("");
      }

      this.editCateForm = res.data;
      this.editDialogVisible = true;
    },
    //
    editPramas() {
      this.$refs.editcateFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editCateForm.attr_id}`,
          {
            attr_name: this.editCateForm.attr_name,
            attr_sel: this.activeName,
          }
        );
        if (res.meta.status !== 200) return this.$message.error("");
        this.$message.success("!");
        this.editDialogVisible = false;
        this.getParmasList();
      });
    },
    //  
    async removeDialog(id) {
      const confirmresult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmresult !== "confirm") return this.$message.info("");

      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${id}`
      );
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      this.getParmasList();
    },
    handleInputConfirm() {
      console.log("ok");
    },
    // 
    showInput(row) {
      row.inputVisible = true;
      // 
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
   handleInputConfirm(row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = "";
        row.inputVisible = false;
        return;
      }
      //return 
      row.attr_vals.push(row.inputValue.trim());
      row.inputValue = "";
      row.inputVisible = false;
  this.saveAttrVals(row)
    },
   async saveAttrVals(row){
       const {data:res} = await this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
        attr_name:row.attr_name,
        attr_sel:row.attr_sel,
        attr_vals:row.attr_vals.join(' ')
      })
      if(res.meta.status!==200){
        return this.$message.error('')
      }
        this.$message.success('')
    },
    //
    handleClose(i,row){
row.attr_vals.splice(i,1)
this.saveAttrVals(row)
    }
  },

  computed: {
    isBtnDisabled() {
      if (this.selectedCateKeys.length !== 3) {
        return true;
      }
      return false;
    },
    // 
    titleText() {
      if (this.activeName == "many") {
        return "";
      }
      return "";
    },
    cateId() {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2];
      }
      return null;
    },
  },
};
</script>

<style lang='less' scope>
.cat_opt {
  margin: 15px 0;
}

.el-tag {
  margin: 10px;
}

.input-new-tag {
  width: 120px !important;
}
</style>