<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        :data="catelist"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
        algin="center"
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-circle-check"
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen"
          ></i>
          <i class="el-icon-circle-close" v-else style="color: red"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            type="success"
            size="mini"
            v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag type="warning" size="mini" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini"
            ></el-button
          >
          <el-button type="danger" icon="el-icon-delete" size="mini"
            ></el-button
          >
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="querInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="querInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateDialogClose"
    >
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="CateFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!-- options -->
          <!-- props -->
          <!-- @change="parentCateChanged" -->
          <el-cascader
            :options="parentCateList"
            :props="cascaderProps"
            v-model="selectedKeys"
            change-on-select
            clearable
            @change="parentCateChanged"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      catelist: [],
      //
      querInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5,
      },
      total: 0,
      // table
      columns: [
        {
          label: "",
          prop: "cat_name",
        },
        {
          label: "",
          //
          type: "template",
          template: "isok",
        },
        {
          label: "",
          //
          type: "template",
          template: "order",
        },
        {
          label: "",
          //
          type: "template",
          template: "opt",
        },
      ],
      //
      addCateDialogVisible: false,
      // 
      addCateForm: {
        //
        cat_name: "",
        //id
        cat_pid: 0,
        //
        cat_level: 0,
      },

      //
      addCateFormRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 6, message: " 3  5 ", trigger: "blur" },
        ],
      },
      // 
      parentCateList: [],
      // 
      cascaderProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
        expandTrigger: "hover",
      },
      // Id
      selectedKeys: [],
    };
  },

  created() {
    this.getCateList();
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get("categories", {
        params: this.querInfo,
      });
      if (res.meta.status !== 200) return this.$message.error("");
      //catelist
      this.catelist = res.data.result;
      //
      // console.log(res);
      this.total = res.data.total;
    },
    //  pagesize 
    handleSizeChange(newSize) {
      this.querInfo.pagesize = newSize;
      this.getCateList();
    },
    //  pagenum 
    handleCurrentChange(newPage) {
      this.querInfo.pagenum = newPage;
      this.getCateList();
    },
    //
    showAddCateDialog() {
      // 
      this.getParentCateList();
      this.addCateDialogVisible = true;
    },
    //
    async getParentCateList() {
      const { data: res } = await this.$http.get("categories", {
        params: { type: 2 },
      });

      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      //   console.log(res.data)
      this.parentCateList = res.data;
    },
    //
    parentCateChanged() {
      // console.log();
      //selectedKeyslength0 
      if (this.selectedKeys.length > 0) {
        //id
        this.addCateForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ];
        this.addCateForm.cat_level = this.selectedKeys.length;
        return;
      } else {
        this.addCateForm.cat_pid = 0;
        this.addCateForm.cat_level = 0;
      }
    },
    //
    addCateDialogClose() {
      this.$refs.CateFormRef.resetFields();
      this.selectedKeys = [];
      this.addCateForm.cat_level = 0;
      this.addCateForm.cat_pid = 0;
    },
    //
    addCate() {
      this.$refs.CateFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post(
          "categories",
          this.addCateForm
        );
        if (res.meta.status !== 201) return this.$message.error("");
        this.$message.success("");
        this.getCateList();
        this.addCateDialogVisible = false;
      });
    },
  },
};
</script>

<style lang='less' scope>
</style>