<template>
<div class="login_container">
  <div class="login_box">
    <!--  -->
    <div class="avatar_box">
      <img src="../assets/X1.jpeg" alt="">
    </div>
    <!--  -->
<el-form ref="loginFormRef" :model="loginForm" label-width="0px" class="login_form" :rules="loginFormRules">
    <!--  -->
  <el-form-item prop="username" >
    <el-input prefix-icon="iconfont icon-user" v-model="loginForm.username"  ></el-input>
  </el-form-item>
  <!--  -->
   <el-form-item prop="password">
    <el-input prefix-icon="iconfont icon-3702mima" v-model="loginForm.password" type="password" ></el-input>
  </el-form-item>
  <!--  -->
  <el-form-item class="btns">
    <el-button type="primary" @click="login"></el-button>
    <el-button type="info" @click="resetLoginForm"></el-button>
  </el-form-item>
  </el-form>
  </div>
</div>
</template>
<script>
export default {
  data(){
    return{
      //
      loginForm:{
           username:'admin',
           password:'123456'
      },
      loginFormRules:{
        //
        username:[  
          { required: true, message: '', trigger: 'blur' }, 
          { min: 3, max: 10, message: ' 3  10', trigger: 'blur' }
          ],
        //
        password:[
          { required: true, message: '', trigger: 'blur' }, 
          { min: 6, max: 15, message: ' 6  15', trigger: 'blur' }
          ]
      }
    }
  },
  methods: {
    resetLoginForm(){
      this.$refs.loginFormRef.resetFields()
    },
    login(){
      this.$refs.loginFormRef.validate(async vali=>{
        // console.log(vali);
        if(!vali) return 
  const {data:res} = await  this.$http.post('login',this.loginForm)
  // console.log(this);
  // console.log(res);
  if(res.meta.status !==200) return this.$message.error('');
  this.$message.success('');
  window.sessionStorage.setItem('token',res.data.token)
  this.$router.push('/home')
      })
    }
  },
}
</script>
<style lang="less" scoped>
.login_container{
  background:url(../assets/x4.jpg) no-repeat;
  background-size: cover;
  height: 100%;
}
.login_box{
  position:relative ;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 450px;
  height: 300px;
  // background-color: #fff;
  background: url(../assets/.jpg) no-repeat;
  background-size: cover;
  border-radius: 5px;
  border-radius: 3px;
  .avatar_box{
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    height: 130px;
    width: 130px;
    border: 1px solid #eee ;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    background-color: #fff;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
  .login_form{
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
  }
  .btns{
    display: flex;
    justify-content: flex-end ;
  }
}
</style>