<template>
  <div>
      <!--  -->
 <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
</el-breadcrumb>
<!--  -->
<el-card>
  <el-button type="primary"></el-button>
   <el-table :data="roleslist" border stripe  >
     <!--  -->
     <el-table-column type="expand">
       <template slot-scope="scope">
         <el-row  v-for="(item1,i1) in scope.row.children" :key="item1.id" :class="['bdbottmn',i1===0?'bdtop':'','vcenter']">
            <!--  -->
            <el-col :span="5">
              <el-tag closable>{{item1.authName}}</el-tag>
              <i class="el-icon-caret-right"></i>
            </el-col>
            <!--  -->
            <el-col :span="19">
              <!--  -->
                  <el-row v-for="(item2,i2) in item1.children" :key="item2.id" :class="[i2===0?'':'bdtop','vcenter']">
                     <el-col :span="6">
                       <el-tag type="success" closable>{{item2.authName}}</el-tag>
                       <i class="el-icon-caret-right"></i>
                     </el-col>
                     <el-col :span="18">
                       <el-tag v-for="(item3) in item2.children" :key="item3.id" type="warning" closable @close="removeRoleById(scope.row,item3.id)">
                         {{item3.authName}}
                       </el-tag>
                     </el-col>
                  </el-row>
            </el-col>
          </el-row>
       </template>
     </el-table-column>
    <el-table-column type="index" label="#"></el-table-column>
    <el-table-column label="" prop="roleName"></el-table-column>
    <el-table-column label="" prop="roleDesc"></el-table-column>
    <el-table-column label="" >
      <template slot-scope="scope">
                <!--  -->   
            <el-tooltip  effect="light" content="" placement="top" :enterable='false' >
                <el-button type="primary" icon="el-icon-edit" circle size="mini" ></el-button>
            </el-tooltip>
                <!--  -->
            <el-tooltip  effect="dark" content="" placement="top" :enterable='false'>
              <el-button type="danger" icon="el-icon-delete" circle size="mini" ></el-button>
            </el-tooltip>
                <!--  -->
            <el-tooltip  effect="dark" content="" placement="top" :enterable='false'>
              <el-button type="warning" icon="el-icon-setting" circle size="mini" @click="showSetRightDialog(scope.row)"></el-button>
            </el-tooltip>
      </template>
    </el-table-column>

</el-table>
</el-card>
<!--  -->

<el-dialog
  title=""
  :visible.sync="setRightDialogVisible"
  width="40%"
  @close="setRightDialogClosed()"
  >
  <!--  -->
  <el-tree :data="rightslist" :props="treeProps" show-checkbox node-key='id' default-expand-all :default-checked-keys="defkeys" ref="treeRef">
    
  </el-tree>
  <span slot="footer" class="dialog-footer">
    <el-button @click="setRightDialogVisible = false"> </el-button>
    <el-button type="primary" @click="allotRights"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
data() {
  return {
    roleslist:[],
    // 
    setRightDialogVisible:false,
    //
    rightslist:[],
    // 
    treeProps:{
      label:'authName',
      children:'children'
    },
    //id
    defkeys:[],
    //id
    roleId:''

  }
},
created() {
  this.getRoleList()
},
methods: {
  async getRoleList(){
    const {data:res} = await this.$http.get('roles')
    if(res.meta.status!==200) return this.$message.console.error('')
    this.roleslist =res.data
  },
  //id
 async removeRoleById(role,rightId){
      //
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err=>err)
      if(confirmResult!=='confirm'){
        return this.$message.info('')
      }
  const {data:res} = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if(res.meta.status!==200) return this.$message.error('')
      this.$message.success('')
      this.getRoleList()
  },
  //
 async showSetRightDialog(role){
   this.roleId=role.id
    //
      const {data:res} = await this.$http.get('rights/tree')
      if(res.meta.status!==200) return this.$message.error('')
      this.rightslist=res.data
      //id
      this.getLeafKeys(role,this.defkeys)
      this.setRightDialogVisible=true
  },
  //
  getLeafKeys(node,arr){
if(!node.children){
  return arr.push(node.id)
}
node.children.forEach(item=>this.getLeafKeys(item,arr))
  },
  //
  setRightDialogClosed(){
this.defkeys=[]
  },
  //
 async allotRights(){
    const keys=[
      ...this.$refs.treeRef.getCheckedKeys(),
      ...this.$refs.treeRef.getHalfCheckedKeys()
    ]
    const idStr =keys.join(',')
  const {data:res}= await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
    if(res.meta.status!==200){
      return this.$message.error('!')
    }
      this.$message.success('')
      this.getRoleList()
      this.setRightDialogVisible = false
  }
},  


}
</script>

<style lang="less" scope>
.el-tag{
  // display: block !important;
margin: 7px;
}
.bdbottmn{
  border-bottom: 1px solid #eee;
}
.bdtop{
  border-top: 1px solid #eee;;
}
.vcenter{
  display: flex;
  align-items: center;
}
</style>